import Navigation from "@/components/Navigation";
import Footer from "@/components/Footer";

const DeclaratieConsimtamant = () => {
  return (
    <>
      <Navigation />

      <main className="min-h-screen bg-background py-40">
        <div className="container mx-auto px-4 lg:px-8">
          <div className="max-w-4xl mx-auto">
            <h1 className="text-3xl md:text-4xl font-bold text-foreground mb-8 text-center">
              Declaraie de consimmnt
            </h1>

            <div className="prose prose-lg max-w-none text-foreground">
              <p>
                Declar prin aceasta c sunt de acord cu participarea copilului
                meu/minorului aflat sub tutela mea la cursurile organizate de
                <strong> Centrul Pro Erudio SRL</strong>.
              </p>

              <p>
                mi exprim n mod expres i neechivoc acordul privind
                prelucrarea urmtoarelor date cu caracter personal ale
                Minorului aflat sub tutela mea: nume i prenume, vrst,
                coala de provenien, de ctre Organizator pe ntreaga
                durat a desfurrii cursurilor.
              </p>

              <p>
                Totodat, sunt de acord ca societatea <strong>Centrul Pro
                Erudio SRL</strong>, cu sediul n <strong>Bd. George Cobuc,
                nr. 69, Sector 5, Bucureti, Romnia</strong>, s fie
                autorizat s proceseze datele mele personale introduse n
                formularul de nregistrare client, precum i datele colectate
                n cadrul tranzaciilor comerciale, n urmtoarele scopuri:
                furnizarea de informaii prin e-mail, SMS, telefon sau
                platforme de social media referitoare la campanii de
                marketing, oferte speciale, evenimente i/sau alte forme de
                publicitate, precum i contactarea n vederea desfurrii
                sondajelor de opinie.
              </p>

              <p>
                Consimmntul privind prelucrarea datelor cu caracter personal
                este voluntar i poate fi revocat n orice moment, cu efect
                ulterior, printr-o notificare gratuit ctre <strong>Centrul
                Pro Erudio SRL</strong>, transmis verbal sau n scris (e-mail
                ctre <strong>office@proerudio.ro</strong>).
              </p>

              <p>
                Revocarea consimmntului nu afecteaz legalitatea utilizrii
                datelor realizate anterior retragerii acestuia.
              </p>

              <p className="text-center font-semibold mt-10">
                V mulumim pentru colaborare!
              </p>

              <p className="text-center font-bold mt-4">
                PRO ERUDIO SCHOOL OF ENGLISH
              </p>
            </div>
          </div>
        </div>
      </main>

      <Footer />
    </>
  );
};

export default DeclaratieConsimtamant;
