import { Plane, Mountain, Sun, Snowflake, Backpack } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Link } from "react-router-dom";

const CoursesSection = () => {
  const camps = [
    {
      icon: Plane,
      title: "Tabr de limb englez si aventur n Marea Britanie",
      subtitle: "Grosvenor Activity Centre",
      description: "Cursuri de limba englez, activiti de aventur in aer liber (rafting, escalada, scrim, canotaj, tir cu arcul, etc.), vizite culturale i concursuri n echipe multinaionale.",
      color: "bg-primary",
      link: "/tabara-marea-britanie-grosvenor-hall-2026",
    },
    {
      icon: Backpack,
      title: "coal internaional de var n Irlanda de Nord",
      subtitle: "Dublin City University",
      description: "Cursuri de limba englez la renumita universitate din Dublin. Cazare n campus universitar, excursii la obiective turistice locale, activiti sportive i seri tematice n grupuri internaionale.",
      color: "bg-emerald-500",
      link: "/scoala-de-vara-dublin-2026",
    },
    {
      icon: Sun,
      title: "Tabr de limba englez n Romnia",
      subtitle: "Moineti, Judeul Bacu",
      description: "Cursuri de limba englez dimineaa, provocri pe echipe la atelierele de dup-amiaz i sear, mese cu bufet suedez, cazare n complex turistic nou i excursii la obiective turistice din zon.",
      color: "bg-amber-500",
      link: "/tabara-moinesti-2026",
    },
    {
      icon: Mountain,
      title: "Tabr de limba englez n Romnia",
      subtitle: "Poiana Mrului, Judeul Braov",
      description: "Tabr n natur, cursuri de limba englez, drumeii, provocri n echipe i seri tematice. Perfect pentru copiii care iubesc aventura.",
      color: "bg-sky-500",
      link: "/tabara-poiana-marului-2026",
    },
  ];

  return (
    <section id="tabere" className="py-10 bg-secondary">
      <div className="container mx-auto px-4 lg:px-8">
        <div className="text-center mb-16">
          <h2 className="text-4xl md:text-5xl font-bold text-foreground mb-4">
            Tipuri de Tabere
          </h2>
          <p className="text-lg text-muted-foreground max-w-2xl mx-auto">
            Alege tabra potrivit pentru copilul tu. Fiecare program combin nvarea limbii engleze cu activiti culturale, sportive, artistice i de divertisment.
          </p>
        </div>
        
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
          {camps.map((camp, index) => (
            <div 
              key={index}
              className="bg-card rounded-2xl p-8 border border-border hover:shadow-lg transition-all duration-300 group"
            >
              <div className={`w-14 h-14 rounded-xl ${camp.color} flex items-center justify-center mb-6`}>
                <camp.icon className="w-7 h-7 text-primary-foreground" />
              </div>
              <h3 className="text-xl font-bold text-foreground mb-2">
                {camp.title}
              </h3>
              <p className="text-primary font-medium mb-3">
                {camp.subtitle}
              </p>
              <p className="text-muted-foreground mb-6">
                {camp.description}
              </p>
              <Button variant="outline" className="rounded-full group-hover:bg-primary group-hover:text-primary-foreground transition-all" asChild>
                <Link to={camp.link}>
                  Vezi Detalii
                </Link>
              </Button>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default CoursesSection;