import Navigation from "@/components/Navigation";
import Footer from "@/components/Footer";
import { Card, CardContent } from "@/components/ui/card";
import {
  Users,
  Award,
  BookOpen,
  Heart,
  Globe,
  Target,
  CheckCircle,
  Mail,
  Phone,
  MapPin,
} from "lucide-react";
import RegistrationForm from "@/components/RegistrationForm";
import {
  Carousel,
  CarouselContent,
  CarouselItem,
  CarouselNext,
  CarouselPrevious,
} from "@/components/ui/carousel";
import aboutClassroomImage from "@/assets/about-classroom.jpg";
import summerCampImage from "@/assets/summer-camp.jpg";
import { Head } from "vite-react-ssg";

const AboutPage = () => {
  // IMPORTANT: pentru OG tags, folosete URL absolut + imagine public accesibil
  // Recomandat: pune o imagine OG n /public/og/despre-noi.jpg (1200x630)
  const canonicalUrl = "https://tabere.proerudio.ro/despre-noi";
  const ogImage = aboutClassroomImage;

  const teamMembers = [
    {
      name: "Maria Popescu",
      role: "Director & Fondator",
      description:
        "Fondator Pro Erudio, cu peste 14 ani de experien n organizarea taberelor de englez.",
      image: summerCampImage,
    },
    {
      name: "Andrei Ionescu",
      role: "Coordonator Tabere Internaionale",
      description:
        "Responsabil pentru taberele din UK i Irlanda, expert n logistic internaional.",
      image: summerCampImage,
    },
    {
      name: "Elena Dumitrescu",
      role: "Director Academic",
      description:
        "Certificat Cambridge CELTA, coordoneaz programele de englez din toate taberele.",
      image: summerCampImage,
    },
    {
      name: "Alexandru Radu",
      role: "Coordonator Activiti & Animaie",
      description:
        "15 ani experien n animaie, creatorul programelor de activiti din tabere.",
      image: summerCampImage,
    },
    {
      name: "Cristina Munteanu",
      role: "Profesor Englez Senior",
      description:
        "Specializat n metode interactive de predare pentru copii i adolesceni.",
      image: summerCampImage,
    },
    {
      name: "Dan Georgescu",
      role: "Animator Principal",
      description:
        "Expert n team building i jocuri educative, adorat de copii n fiecare tabr.",
      image: summerCampImage,
    },
    {
      name: "Ioana Stanciu",
      role: "Profesor Englez",
      description:
        "Specializat n pregtire Cambridge, particip la taberele internaionale.",
      image: summerCampImage,
    },
    {
      name: "Mihai Popa",
      role: "Coordonator Sport & Outdoor",
      description:
        "Instructor de drumeii i sporturi, responsabil pentru activitile outdoor.",
      image: summerCampImage,
    },
  ];

  const values = [
    {
      icon: Heart,
      title: "nvare prin Aventur",
      description:
        "Credem c engleza se nva cel mai bine prin experiene memorabile i activiti captivante.",
    },
    {
      icon: Users,
      title: "Siguran pe Primul Loc",
      description:
        "Supraveghere 24/7, protocoale stricte i echip dedicat pentru sigurana fiecrui copil.",
    },
    {
      icon: Award,
      title: "Excelen n Educaie",
      description:
        "Profesori certificai Cambridge i metode dovedite pentru progres real n englez.",
    },
    {
      icon: Globe,
      title: "Experiene Internaionale",
      description:
        "Tabere n UK, Irlanda i Romnia pentru imersie cultural autentic.",
    },
  ];

  const milestones = [
    { year: "2010", event: "Prima tabr de englez Pro Erudio n Romnia" },
    { year: "2012", event: "Lansarea primei tabere internaionale n Londra" },
    { year: "2014", event: "Extinderea n Irlanda - Dublin i Cork" },
    { year: "2016", event: "Peste 1000 de copii participani la tabere" },
    { year: "2019", event: "Deschiderea taberelor de iarn i Pate" },
    { year: "2023", event: "Peste 2500 de absolveni i 50+ ediii de tabere" },
  ];

  return (
    <div className="min-h-screen bg-background">
      {/*  SEO + Open Graph (Facebook/WhatsApp) */}
      <Head>
        <title>Despre noi  Tabere de Englez Pro Erudio</title>

        <meta
          name="description"
          content="Afl povestea Pro Erudio: peste 14 ani de experien n tabere de englez n Romnia i n strintate, cu profesori dedicai i activiti memorabile."
        />

        <link rel="canonical" href={canonicalUrl} />

        <meta property="og:type" content="website" />
        <meta property="og:url" content={canonicalUrl} />
        <meta property="og:title" content="Despre noi  Tabere de Englez Pro Erudio" />
        <meta
          property="og:description"
          content="Peste 14 ani de experien n tabere de englez n Romnia, UK i Irlanda. Profesori dedicai, siguran i experiene memorabile."
        />
        <meta property="og:image" content={ogImage} />
        <meta property="og:image:width" content="1200" />
        <meta property="og:image:height" content="630" />

        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:title" content="Despre noi  Tabere de Englez Pro Erudio" />
        <meta
          name="twitter:description"
          content="Povestea Pro Erudio, echipa i valorile noastre. Tabere de englez n Romnia i n strintate."
        />
        <meta name="twitter:image" content={ogImage} />
      </Head>

      <Navigation />

      {/* Hero Section */}
      <section className="relative pt-16">
        <div className="h-[50vh] relative overflow-hidden">
          <img
            src={aboutClassroomImage}
            alt="Despre Pro Erudio"
            className="w-full h-full object-cover"
          />
          <div className="absolute inset-0 bg-gradient-to-t from-background via-background/60 to-transparent" />
          <div className="absolute bottom-0 left-0 right-0 p-8 lg:p-16">
            <div className="container mx-auto">
              <span className="inline-block bg-accent text-accent-foreground px-4 py-1 rounded-full text-sm font-semibold mb-4">
                Despre Noi
              </span>
              <h1 className="text-4xl md:text-6xl font-bold text-foreground mb-4">
                Tabere de Englez Pro Erudio
              </h1>
              <p className="text-xl text-muted-foreground max-w-2xl">
                Peste 14 ani de experien n organizarea taberelor educaionale
              </p>
            </div>
          </div>
        </div>
      </section>

      {/* Mission Section */}
      <section className="py-20">
        <div className="container mx-auto px-4 lg:px-8">
          <div className="grid lg:grid-cols-2 gap-12 items-center">
            <div>
              <div className="flex items-center gap-3 mb-6">
                <Target className="h-8 w-8 text-primary" />
                <h2 className="text-3xl md:text-4xl font-bold text-foreground">
                  Misiunea Noastr
                </h2>
              </div>
              <p className="text-muted-foreground mb-6 leading-relaxed">
                Pro Erudio organizeaz tabere de limb englez din 2010, cu convingerea c cea mai bun
                modalitate de a nva o limb strin este prin imersie i experiene de neuitat.
                Taberele noastre combin cursuri intensive de englez cu aventur, sport i cultur.
              </p>
              <p className="text-muted-foreground mb-6 leading-relaxed">
                Misiunea noastr este s oferim copiilor i adolescenilor oportunitatea de a nva
                engleza ntr-un mediu internaional, sigur i distractiv, unde fac prieteni din toat
                lumea i creeaz amintiri pentru o via.
              </p>
              <div className="space-y-3">
                <div className="flex items-center gap-3">
                  <CheckCircle className="h-5 w-5 text-accent flex-shrink-0" />
                  <span className="text-foreground">
                    Peste 50 de ediii de tabere organizate
                  </span>
                </div>
                <div className="flex items-center gap-3">
                  <CheckCircle className="h-5 w-5 text-accent flex-shrink-0" />
                  <span className="text-foreground">
                    Destinaii n Romnia, UK i Irlanda
                  </span>
                </div>
                <div className="flex items-center gap-3">
                  <CheckCircle className="h-5 w-5 text-accent flex-shrink-0" />
                  <span className="text-foreground">
                    Profesori certificai Cambridge i echip dedicat
                  </span>
                </div>
              </div>
            </div>
            <div className="rounded-2xl overflow-hidden shadow-lg">
              <img
                src={summerCampImage}
                alt="Misiunea Pro Erudio"
                className="w-full h-[400px] object-cover"
              />
            </div>
          </div>
        </div>
      </section>

      {/* Values Section */}
      <section className="py-20 bg-secondary/30">
        <div className="container mx-auto px-4 lg:px-8">
          <div className="text-center mb-12">
            <h2 className="text-3xl md:text-4xl font-bold text-foreground mb-4">
              Valorile Noastre
            </h2>
            <p className="text-muted-foreground max-w-2xl mx-auto">
              Principiile care ne ghideaz n tot ceea ce facem
            </p>
          </div>
          <div className="grid sm:grid-cols-2 lg:grid-cols-4 gap-6">
            {values.map((value, index) => (
              <Card
                key={index}
                className="bg-card border-border text-center hover:shadow-lg transition-shadow"
              >
                <CardContent className="pt-8 pb-6">
                  <div className="w-16 h-16 rounded-full bg-primary/10 flex items-center justify-center mx-auto mb-4">
                    <value.icon className="h-8 w-8 text-primary" />
                  </div>
                  <h3 className="text-xl font-bold text-foreground mb-2">
                    {value.title}
                  </h3>
                  <p className="text-muted-foreground">{value.description}</p>
                </CardContent>
              </Card>
            ))}
          </div>
        </div>
      </section>

      {/* Timeline Section */}
      <section className="py-20">
        <div className="container mx-auto px-4 lg:px-8">
          <div className="text-center mb-12">
            <div className="flex items-center gap-3 justify-center mb-4">
              <BookOpen className="h-8 w-8 text-primary" />
              <h2 className="text-3xl md:text-4xl font-bold text-foreground">
                Povestea Noastr
              </h2>
            </div>
            <p className="text-muted-foreground max-w-2xl mx-auto">
              Momente importante din istoria Pro Erudio
            </p>
          </div>
          <div className="max-w-3xl mx-auto">
            <div className="space-y-6">
              {milestones.map((milestone, index) => (
                <div key={index} className="flex items-center gap-6">
                  <span className="text-2xl font-bold text-primary min-w-[80px]">
                    {milestone.year}
                  </span>
                  <div className="w-4 h-4 rounded-full bg-accent flex-shrink-0" />
                  <p className="text-foreground text-lg">{milestone.event}</p>
                </div>
              ))}
            </div>
          </div>
        </div>
      </section>

      {/* Team Section */}
      <section className="py-20 bg-secondary/30">
        <div className="container mx-auto px-4 lg:px-8">
          <div className="text-center mb-12">
            <div className="flex items-center gap-3 justify-center mb-4">
              <Users className="h-8 w-8 text-primary" />
              <h2 className="text-3xl md:text-4xl font-bold text-foreground">
                Echipa Noastr
              </h2>
            </div>
            <p className="text-muted-foreground max-w-2xl mx-auto">
              Profesorii i animatorii care fac taberele noastre speciale
            </p>
          </div>
          <Carousel
            opts={{
              align: "start",
              loop: true,
            }}
            className="w-full"
          >
            <CarouselContent className="-ml-4">
              {teamMembers.map((member, index) => (
                <CarouselItem
                  key={index}
                  className="pl-4 basis-full sm:basis-1/2 lg:basis-1/4"
                >
                  <Card className="bg-card border-border overflow-hidden hover:shadow-lg transition-shadow h-full">
                    <div className="h-48 overflow-hidden">
                      <img
                        src={member.image}
                        alt={member.name}
                        className="w-full h-full object-cover"
                      />
                    </div>
                    <CardContent className="pt-4">
                      <h3 className="text-lg font-bold text-foreground">
                        {member.name}
                      </h3>
                      <p className="text-primary font-medium text-sm mb-2">
                        {member.role}
                      </p>
                      <p className="text-muted-foreground text-sm">
                        {member.description}
                      </p>
                    </CardContent>
                  </Card>
                </CarouselItem>
              ))}
            </CarouselContent>
            <div className="flex justify-center gap-4 mt-8">
              <CarouselPrevious className="static translate-y-0 bg-primary text-primary-foreground hover:bg-primary/90" />
              <CarouselNext className="static translate-y-0 bg-primary text-primary-foreground hover:bg-primary/90" />
            </div>
          </Carousel>
        </div>
      </section>

      {/* Contact Section */}
      <section className="py-20">
        <div className="container mx-auto px-4 lg:px-8">
          <div className="text-center">
            <h2 className="text-3xl md:text-4xl font-bold text-foreground mb-6">
              ntrebri despre Tabere?
            </h2>
            <p className="text-muted-foreground mb-8 leading-relaxed max-w-2xl mx-auto">
              Vrei s afli mai multe despre taberele noastre? Suntem aici s rspundem la toate
              ntrebrile tale despre program, locaii, preuri i nscrieri.
            </p>
          </div>
          <div className="flex flex-wrap gap-8 justify-center">
            <div className="flex items-center gap-4">
              <div className="w-12 h-12 rounded-full bg-primary/10 flex items-center justify-center">
                <Phone className="h-6 w-6 text-primary" />
              </div>
              <div>
                <p className="text-sm text-muted-foreground">Telefon</p>
                <p className="font-semibold text-foreground">+40 722 123 456</p>
              </div>
            </div>
            <div className="flex items-center gap-4">
              <div className="w-12 h-12 rounded-full bg-primary/10 flex items-center justify-center">
                <Mail className="h-6 w-6 text-primary" />
              </div>
              <div>
                <p className="text-sm text-muted-foreground">Email</p>
                <p className="font-semibold text-foreground">contact@proerudio.ro</p>
              </div>
            </div>
            <div className="flex items-center gap-4">
              <div className="w-12 h-12 rounded-full bg-primary/10 flex items-center justify-center">
                <MapPin className="h-6 w-6 text-primary" />
              </div>
              <div>
                <p className="text-sm text-muted-foreground">Adres</p>
                <p className="font-semibold text-foreground">Bucureti, Sector 1</p>
              </div>
            </div>
          </div>
        </div>
      </section>

      {/* Registration Form Section */}
      <section className="py-20 bg-primary">
        <div className="container mx-auto px-4 lg:px-8">
          <div className="text-center mb-12">
            <h2 className="text-3xl md:text-4xl font-bold text-primary-foreground mb-4">
              Rezerv un Loc la Tabr!
            </h2>
            <p className="text-primary-foreground/80 max-w-2xl mx-auto">
              nscrierile pentru taberele 2025 sunt deschise. Locurile sunt limitate!
            </p>
          </div>
          <div className="max-w-3xl mx-auto">
            <Card className="bg-card border-0 shadow-2xl">
              <CardContent className="p-8">
                <RegistrationForm />
              </CardContent>
            </Card>
          </div>
        </div>
      </section>

      <Footer />
    </div>
  );
};

export default AboutPage;
