import { Button } from "@/components/ui/button";
import englishTestImage from "@/assets/online test.avif";
import { Link } from "react-router-dom";

const EnglishTestSection = () => {
  return (
    <section className="py-10 bg-secondary/30">
      <div className="container mx-auto px-4 lg:px-8">
        <div className="grid lg:grid-cols-2 gap-12 items-center">
          <div>
            <h2 className="text-3xl md:text-4xl font-bold text-foreground mb-6">
              Test de plasare pentru taberele de limba englez
            </h2>
            <p className="text-muted-foreground mb-6 leading-relaxed">
             Te-ai nscris la taberele noastre, dar nu urmezi cursuri la Pro Erudio? 
             Nicio problem! Cu cteva sptmni nainte de nceperea taberei vei primi n email un test online gratuit pentru a te plasa corect n grupele de limba englez.
            </p>
            <p className="text-muted-foreground mb-8 leading-relaxed">
              Testul dureaz aproximativ 15-20 de minute i i evalueaz competenele de
              vocabular, gramatic i exprimare scris n limba englez. Folosirea oricrei surse de informaie (internet, prieteni, cri) este strict interzis.
            </p>
            <p className="text-muted-foreground mb-8 leading-relaxed">
              Mult succes!
            </p>
            <Button
              asChild
              size="lg"
              className="bg-accent hover:bg-accent/90 text-accent-foreground font-semibold rounded-full px-8"
            >
              {/* <Link to="/test-de-plasare">
                ncepe Testul Gratuit
              </Link> */}
            </Button>
          </div>
          <div className="rounded-2xl overflow-hidden shadow-lg">
            <img
              src={englishTestImage}
              alt="Test de amplasament nivel englez"
              className="w-full h-[400px] object-cover"
            />
          </div>
        </div>
      </div>
    </section>
  );
};

export default EnglishTestSection;
